  font-family: "Scada Bold";
  src: url("/fonts/Scada-Bold.ttf") format("truetype");
}

@font-face {
  font-family: "Scada Regular";
  src: url("/fonts/Scada-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Scada BoldItalic";
  src: url("/fonts/Scada-BoldItalic.ttf") format("truetype");
}

@font-face {
  font-family: "Scada Italic";
  src: url("/fonts/Scada-Italic.ttf") format("truetype");
}

@font-face {
  font-family: "Roboto-Medium";
  src: url("/fonts/Roboto-Medium.ttf") format("truetype");
}

@font-face {
  font-family: "RobotoCondensed-Light";
  src: url("/fonts/RobotoCondensed-Light.ttf") format("truetype");
}

@font-face {
  font-family: "RobotoCondensed-Regular";
  src: url("/fonts/RobotoCondensed-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Roboto-Regular";
  src: url("/fonts/Roboto-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Roboto Light";
  src: url("/fonts/Roboto-Light.ttf") format("truetype");
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Scada Bold";
  font-size: 1.25rem;
  border-radius: 50px;
  border: 1px solid #FFFFFF;
  text-align: center;
  text-decoration: none;
  color: #ffffff;
  box-sizing: border-box;
  text-transform: uppercase;
  width: 277px;
  height: 60px;
}

.button--hover {
  background: #89C443;
  color: #FFFFFF;
}

.slick-loading .slick-list {
  background: #fff url("./ajax-loader.gif") center center no-repeat;
}

@font-face {
  font-family: "slick";
  src: url("./fonts/slick.eot");
  src: url("./fonts/slick.eot?#iefix") format("embedded-opentype"),url("./fonts/slick.woff") format("woff"),url("./fonts/slick.ttf") format("truetype"),url("./fonts/slick.svg#slick") format("svg");
  font-weight: normal;
  font-style: normal;
}

.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}

.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: .25;
}

.slick-prev:before,
.slick-next:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  color: #fff;
  opacity: .75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}

[dir="rtl"] .slick-prev {
  left: auto;
  right: -25px;
}

.slick-prev:before {
  content: "←";
}

[dir="rtl"] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: -25px;
}

[dir="rtl"] .slick-next {
  left: -25px;
  right: auto;
}

.slick-next:before {
  content: "→";
}

[dir="rtl"] .slick-next:before {
  content: "←";
}

.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: -25px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  height: 20px;
  width: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  border: 0;
  background: transparent;
  display: block;
  height: 20px;
  width: 20px;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: transparent;
  padding: 5px;
  cursor: pointer;
}

.slick-dots li button:hover,
.slick-dots li button:focus {
  outline: none;
}

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  width: 20px;
  height: 20px;
  font-family: "slick";
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: #000;
  opacity: .25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
  color: #000;
  opacity: .75;
}

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

html {
  -webkit-text-size-adjust: 100%;
  overflow-x: hidden;
  font-family: "Roboto-Regular";
  color: #282B4F;
}

hr {
  box-sizing: content-box;
  overflow: visible;
}

body {
  margin: 0;
}

div {
  box-sizing: border-box;
}

main {
  display: block;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}

button,
input {
  overflow: visible;
  box-sizing: border-box;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button,
button:active,
button:focus {
  outline: none;
}

p {
  font-family: "Roboto Light";
  font-size: 1.875rem;
  color: #282B4F;
  margin: 15px 0;
}

h1 {
  font-family: "Scada Regular";
  font-size: 3rem;
  line-height: 60px;
  text-transform: uppercase;
  margin: 0;
  color: #282B4F;
  text-align: center;
}

h2 {
  font-family: "Scada Regular";
  font-size: 2.25rem;
  line-height: 45px;
  margin: 0;
  font-weight: normal;
  text-transform: uppercase;
  color: #282B4F;
}

h3 {
  font-family: "Scada Regular";
  font-size: 2rem;
  line-height: 39px;
  margin: 0;
  font-weight: normal;
  color: #282B4F;
}

.container {
  margin: 0 auto;
  box-sizing: border-box;
  max-width: 1920px;
}

a {
  font-family: "Roboto Light";
  font-size: 1.5rem;
  color: #282B4F;
  background-color: transparent;
  cursor: pointer;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.header {
  max-width: 1920px;
  margin: 0 auto;
}

.header .container_header {
  width: 100%;
}

.header .container_header .background_img {
  width: 100%;
  height: auto;
  overflow: hidden;
  z-index: -5;
}

.header .container_header .centered {
  position: absolute;
  top: -50%;
  left: -50%;
  right: -50%;
  bottom: -50%;
  margin: auto;
}

.header .padded-container {
  height: 0;
  position: relative;
  overflow: hidden;
  padding-bottom: 50%;
  overflow: hidden;
}

.header .top_background {
  position: relative;
}

.header .top_background .wrapper {
  width: 100%;
  z-index: 5;
}

.header .top_background .wrapper .background_img {
  width: 100%;
  height: auto;
  overflow: hidden;
  z-index: -2;
}

.header .top_background .wrapper .centered {
  position: absolute;
  top: -50%;
  left: -50%;
  right: -50%;
  bottom: -50%;
  margin: auto;
}

@media only screen and (min-width: 1024px) and (max-width: 1559px) {
  .button {
    font-size: 1.15rem;
    width: 250px;
  }

  .button a {
    padding-top: 1px;
  }
}